@import url("https://fonts.googleapis.com/css2?family=Public+Sans:ital,wght@0,100..900;1,100..900&display=swap");
@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.11.3/font/bootstrap-icons.min.css");
* {
  font-family: "Public Sans", sans-serif;
}

.App {
  background-color: #4062bb;
  background-image: linear-gradient(316deg, #4062bb 0%, #5200ae 74%);
}

.heading {
  color: #151515;
  font-weight: 700;
}

.btn-primary {
  background-color: #8923f7;
  border-radius: 10px;
  color: #fff;
  border: solid 1px #8923f7;
  font-weight: 500;
}
.btn-primary:hover, .btn-primary:active {
  background-color: #fff !important;
  color: #8923f7 !important;
  border: solid 1px #8923f7 !important;
}

.btn-secondary {
  background-color: #fff;
  border-radius: 10px;
  color: #8923f7;
  border: solid 1px #8923f7;
  font-weight: 500;
}
.btn-secondary:hover, .btn-secondary:active {
  background-color: #8923f7 !important;
  color: #fff !important;
  border: solid 1px #8923f7 !important;
}
.btn-secondary.active {
  background-color: #fff !important;
  color: #8923f7 !important;
  border: solid 2px #8923f7 !important;
  font-weight: 700;
}
.btn-secondary:disabled {
  background-color: #fff;
  border-radius: 10px;
  color: #8923f7;
  border: solid 1px #8923f7;
  font-weight: 500;
}
.btn-secondary:disabled.default {
  border: solid 1px #c73659 !important;
  color: #c73659 !important;
}
.btn-secondary:disabled.correct {
  border: solid 1px #51fb6a !important;
  color: #151515 !important;
  background-color: #51fb6a !important;
}

.header p {
  color: #fff;
  font-weight: 700;
  font-size: 40px;
}

.main-container {
  height: 90vh;
}
.main-container .box {
  overflow: hidden;
  border-radius: 20px;
  width: 100%;
  min-height: 300px;
  background-color: #fff;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}
.main-container .box .select-container .form-select {
  border: solid 1px #8923f7;
  box-shadow: none !important;
}
.main-container .question-box {
  border-radius: 20px;
  width: 100%;
  min-height: 300px;
  background-color: #fff;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}

.error-modal {
  width: 100vw;
  height: 100vh;
  position: absolute;
  inset: 0;
  background-color: rgba(255, 255, 255, 0.6980392157);
}
.error-modal .error-container {
  background-color: #8923f7;
  border-radius: 10px;
  color: #fff;
}
.error-modal .error-container h3 {
  font-weight: 700;
}

.status {
  overflow: hidden;
  border-radius: 20px;
  width: 100%;
  min-height: 300px;
  background-color: #fff;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}
.status .heading {
  color: #8923f7;
}
.status .row-stats {
  border-bottom: solid 1px;
}

.footer-main {
  height: 10vh;
}
.footer-main a {
  height: -moz-fit-content;
  height: fit-content;
}
.footer-main .bi {
  height: -moz-fit-content;
  height: fit-content;
  font-weight: 700;
  font-size: 30px;
  color: #fff;
}
.footer-main .author-text {
  height: -moz-fit-content;
  height: fit-content;
  font-weight: 700;
  font-size: 20px;
  color: #fff;
}/*# sourceMappingURL=style.css.map */